.black{
    color: black;
}
.flex{
    display: flex;
}
p{
    margin-right: 10px;
}

input{
    padding-left: 5px;
}
img{
    height: 100px;
    width: 100px;   
}

.left{
    padding-left: 20px;
    padding-right: 30px;
}
.center{
    justify-content: center;
}
.container{
    width: 100vw;
    height: 100vh;
    background-color: #F3F6FA;
    
}
.weather{
    
    border-radius: 10px;
    background-color: #F3F6FA;
    padding-top: 10%;
    padding-bottom: 10%;
}

.container .city input{
    padding: 5px;
    border: 1px solid #ccc;
    background-color: white;

}
.temp{
    font-size: xx-large;
    font-weight: 1000;
}
.temp_f{
    font-size: medium;
}


.calendar{
    padding-top: 40px;
    display: flex;
    color: #888;
}
.image{
    height: 64px;
    width: 64px;
}

.current_weather{
    margin-top: 30px;
    display: flex;
    gap: 10px;
    align-items: center;
    background-color: white;
    padding-top: 15px;
    padding-bottom: 10px;
    padding-right: 10px;
}

.current_humity{
    margin-top: 40px;
    display: flex;
    gap: 20px;
    align-items: center;
    color: #888;

}

.forecast{
    text-align: center;
    font-size: small;
}

.fcast{
    width: 33%;
    border-radius: 5px;
    font-weight: 700;
}

.today{
    color: #D1E2FC;
    background-color: #5596F6;
}

.day{
    padding-top: 10px;
}

.Humidity{
    padding-bottom: 10px;
}
.other1, .other2{
    color: #000;
}
.current_day{
    display: flex;
    justify-content: center     ;
    font-weight: 1000;
    font-size: x-large;

}
.chart{
    padding-bottom: 30px;
}
.chart-content{
    height: 180px;
    width: 360px;
}

.fcast.selected {
    color: #D1E2FC;
    background-color: #5596F6;
}
  
.fcast.not-selected {
    background-color: white;  /* Màu nền khi không được chọn */
    color: #000
}
  